import React from "react";
import { Row, Col } from "reactstrap";
import Swal from "sweetalert";
import { connect } from "react-redux";
import ClipLoader from "react-spinners/ClipLoader";

import Card from "../../Card/Card.jsx";

import { updateTrashStore, clearTrashStore, updateMovies } from "../../../actions/index.js";

import { store, override } from "../../../helpers/helpers.js";

const TrashComponent = ({ componentName, ...props }) => {
	const { updateTrashStore, clearTrashStore, trashStore } = props;
	const [loading, setLoading] = React.useState(true);
	const handleClearStore = () => {
		return Swal({
			title: "Etes-vous sûr de vouloir effectuer cette action ?",
			text: "",
			icon: "warning",
			buttons: {
				cancel: "Annulez",
				confirm: "Oui",
			},
			dangerMode: true,
		}).then((ok) => {
			clearTrashStore();
		});
	};

	React.useEffect(() => {
		const movies = JSON.parse(window.localStorage.getItem(store["trash"])) || [];

		updateTrashStore(movies);
		setTimeout(() => setLoading(false), 1000);
	}, []);

	if (loading) return <ClipLoader css={override} size={150} loading={loading} />;

	return (
		<div id="trash">
			{trashStore.length > 0 ? (
				<>
					<button
						onClick={handleClearStore}
						type="button"
						className="btn btn-warning clear-store"
					>
						Videz le store
					</button>

					<hr />

					<Row>
						{trashStore.map((movie, i) => (
							<Col md="4" key={i}>
								<Card i={i} {...props} trash movie={movie} />
							</Col>
						))}
					</Row>
				</>
			) : (
				<div className="nothing-to-display">Aucun contenu à afficher...</div>
			)}
		</div>
	);
};

const mapStateToProps = (state) => ({
	trashStore: state.trashStore,
	movies: state.movies,
});
const mapDispatchToProps = {
	updateTrashStore,
	clearTrashStore,
	updateMovies,
};

export default connect(mapStateToProps, mapDispatchToProps)(TrashComponent);
