import React from "react";
import { Container, ListGroup, ListGroupItem, Row, Col } from "reactstrap";

import "./UseCase.css";

const UseCase = () => {
	const cases = [
		"L'utilisateur peut faire une pagination",
		"L'utilisateur peut selectionner le nombre de carte à afficher sur la page",
		"L'utilisateur peut filtrer par catégories",
		"L'utilisateur peut effectuer une recherche sur la vue",
		"L'utilisateur peut aimé un film",
		"L'utilisateur peut ne pas aimé un film",
		"L'utilisateur peut annulez son vote",
		"L'utilisateur peut supprimé un film",
		"L'utilisateur peut voir l'historique des films qu'il à déja aimer",
		"L'utilisateur peut voir l'historique des films qu'il n'à pas aimer",
		"L'utilisateur peut restaurer un film depuis la corbeille",
		"L'utilisateur peut afficher le contenu d'une description en cliquant sur la description de la carte concernée",
		"L'utilisateur peut accéder à la page détails d'un film en cliquant sur l'image du film concerné",
		"L'utilisateur peut vider la corbeille",
		"L'utilisateur peut vider le store des films déjà aimés",
		"L'utilisateur peut vider le store des films non aimés",
	];

	return (
		<Container id="use-case">
			<ListGroup>
				{cases.map((c, i) => (
					<ListGroupItem key={i}>
						<Row>
							<Col>{c}</Col>
						</Row>
					</ListGroupItem>
				))}
			</ListGroup>
		</Container>
	);
};

export default UseCase;
