import React from "react";
import Swal from "sweetalert";
import { connect } from "react-redux";

import { paginate, setItemToDisplay, filterByCategories } from "../../actions/index.js";
import {
	categoriesNames,
	getPagingRange,
	updateUrlQueries,
	noOdds,
} from "../../helpers/helpers.js";

import "./Pagination.css";

class PaginationComponent extends React.Component {
	constructor(props) {
		super(props);
	}

	swal = (
		text = "La recherche est active, veuillez videz le champs de recherche avant de continuer"
	) => Swal({ title: "Oops!", text, icon: "info" });

	handlePaginate = (action) => {
		const { paginate, pagination, filteredBy } = this.props;
		const { page, total_pages } = pagination;

		if (filteredBy.length > 0)
			return this.swal("Veuillez desactiver le filtre avant de continuer");

		const next = action === "next";

		if (typeof action === "string") {
			if (next && page > total_pages) return; // Unable to paginate to paginate (next)
			if (!next && page <= 0) return; // Unable to return (prev)
			if (next) {
				pagination.page++;
			} else {
				pagination.page--;
			}
		} else {
			pagination.page = action;
		}

		updateUrlQueries(this.props, { page: pagination.page });
		paginate(this.props);
	};

	handleItemToDisplay = (evt) => {
		const nbr = parseInt(evt.target.value);
		const { setItemToDisplay } = this.props;

		updateUrlQueries(this.props, { "to-display": nbr });
		setItemToDisplay(nbr);
	};

	disabled = () => {
		const { movies, searchValue } = this.props;
		const disabled = movies.length <= 4 || searchValue !== "";

		return disabled;
	};

	handleFilterByCategories = (categorie) => {
		const { filterByCategories, itemToDisplay } = this.props;

		filterByCategories(this.props, categorie);
		updateUrlQueries(this.props, {
			"filtered-by": categorie === "reset" ? "" : categorie,
			"to-display": categorie !== "reset" ? 0 : itemToDisplay,
		});
	};

	dislayCategories = () => {
		const { categories } = this.props;

		return categoriesNames(categories).map((c, i) => (
			<li onClick={() => this.handleFilterByCategories(c)} key={c}>
				<a href="celebritygrid01.html">{c}</a>
			</li>
		));
	};

	render = () => {
		const { pagination, movies, filteredBy, itemToDisplay } = this.props;
		const { page, total_pages } = pagination;
		const prevDisabled = page <= 1;
		const nextDisabled = page >= total_pages;
		const paginateRange = getPagingRange(page, { total: total_pages, length: 4 });
		const toDisplayRange2 = getPagingRange(1, { total: movies.length, length: movies.length });

		if (filteredBy !== "") return <div />;

		return (
			<div data-testid="pagination">
				<div className="topbar-filter">
					<label>Films par page:</label>
					<select data-testid="item-to-display" onChange={this.handleItemToDisplay}>
						{noOdds(toDisplayRange2, movies.length <= 6 ? 1 : 3).map((nbr, i) => {
							return (
								<option key={i} value={nbr}>
									{nbr} Films
								</option>
							);
						})}
					</select>
					<div className="pagination2">
						<span>Page 1 / {total_pages}:</span>

						{!prevDisabled && (
							<button type="button" onClick={() => this.handlePaginate("prev")}>
								<i className="fa fa-caret-left"></i>
							</button>
						)}

						{paginateRange.map((value, i) => {
							const disabled = value == page;

							return (
								<button
									type="button"
									className={disabled ? "disabled" : ""}
									onClick={() => this.handlePaginate(value)}
									key={i}
								>
									{value}
								</button>
							);
						})}

						{!nextDisabled && (
							<button type="button" onClick={() => this.handlePaginate("next")}>
								<i className="fa fa-caret-right"></i>
							</button>
						)}
					</div>
				</div>
			</div>
		);
	};
}

const mapStateToProps = (state) => ({
	pagination: state.pagination,
	categories: state.categories,
	movies: state.movies,
	keepMovies: state.keepMovies,
	filteredBy: state.filteredBy,
	itemToDisplay: state.itemToDisplay,
	searchValue: state.searchValue,
});
const mapDispatchToProps = { paginate, setItemToDisplay, filterByCategories };

export default connect(mapStateToProps, mapDispatchToProps)(PaginationComponent);
