import React from "react";
import { connect } from "react-redux";
import DotLoader from "react-spinners/DotLoader";
import { Row, Col, TabContent, TabPane, Nav, NavItem, NavLink, Container } from "reactstrap";

import SearchComponent from "../Search/Search.jsx";
import Pagination from "../Pagination/Pagination.jsx";
import Historical from "../Historical/Historical.jsx";
import UseCase from "../UseCase/UseCase.jsx";

import { setMovies, setLoading } from "../../actions/index.js";
import Card from "../Card/Card.jsx";
import { fetchMovies, updateUrlQueries, override } from "../../helpers/helpers.js";

import "./Movies.css";

const MoviesComponent = (props) => {
	const { itemToDisplay, movies, loadingApp, filteredMovies, setMovies, setLoading } = props;
	const [activeTab, setActiveTab] = React.useState("1");

	React.useEffect(() => {
		setLoading(true);

		const { page } = updateUrlQueries(props, {
			page: 1,
			"filtered-by": "",
			"to-display": 12,
		});

		fetchMovies(page, (data) => setMovies(data, page));
	}, []);

	if (loadingApp) {
		return (
			<div data-testid="loading">
				<DotLoader size={50} css={override} loading={loadingApp} />
			</div>
		);
	}

	const data = filteredMovies.length > 0 ? filteredMovies : movies;

	return (
		<div className="movie-items">
			<Container>
				<div className="row ipad-width">
					<div className="col-md-12">
						<div className="title-hd">
							<h2>CINÉMA</h2>
						</div>

						<Nav tabs>
							<NavItem>
								<NavLink
									className={`${activeTab === "1" ? "active" : ""}`}
									onClick={() => setActiveTab("1")}
								>
									<i className="fa fa-circle"></i>
									&nbsp; Films
								</NavLink>
							</NavItem>
							<NavItem>
								<NavLink
									className={`${activeTab === "2" ? "active" : ""}`}
									onClick={() => setActiveTab("2")}
								>
									<i className="fa fa-circle"></i>
									&nbsp; Mon historique
								</NavLink>
							</NavItem>
							<NavItem>
								<NavLink
									className={`${activeTab === "3" ? "active" : ""}`}
									onClick={() => setActiveTab("3")}
								>
									<i className="fa fa-circle"></i>
									&nbsp; Cas d'utilisation
								</NavLink>
							</NavItem>
						</Nav>
						<TabContent activeTab={activeTab}>
							<TabPane tabId="1">
								{data.length > 0 && <SearchComponent />}
								{activeTab === "1" && (
									<Row className="cards-container" data-testid="cards-container">
										{data.length > 0 ? (
											data.map((movie, i) => {
												if (i + 1 <= itemToDisplay) {
													return (
														<Col md="4" key={i}>
															<Card {...props} movie={movie} />
														</Col>
													);
												}
												return <React.Fragment key={i} />;
											})
										) : (
											<div
												data-testid="nothing-to-display"
												className="nothing-to-display"
											>
												Aucun contenu à afficher...
											</div>
										)}
									</Row>
								)}
								{data.length > 0 && <Pagination {...props} />}
							</TabPane>
							<TabPane tabId="2">
								{activeTab === "2" && <Historical {...props} />}
							</TabPane>
							<TabPane tabId="3">{activeTab === "3" && <UseCase />}</TabPane>
						</TabContent>
					</div>
				</div>
			</Container>
		</div>
	);
};

const mapStateToProps = (state) => ({
	pagination: state.pagination,
	loadingApp: state.loadingApp,
	movies: state.movies,
	filteredMovies: state.filteredMovies,
	categories: state.categories,
	filteredBy: state.filteredBy,
	itemToDisplay: state.itemToDisplay,
});
const mapDispatchToProps = {
	setMovies,
	setLoading,
};

export default connect(mapStateToProps, mapDispatchToProps)(MoviesComponent);
