import React from "react";
import { NavLink } from "react-router-dom";

import "./Header.css";

const Header = () => {
	React.useEffect(() => {
		window.onscroll = () => {
			let scrollPos = document.body.scrollTop || document.documentElement.scrollTop,
				header = document.getElementById("ht-header");

			if (header) {
				if (scrollPos > 300) {
					header.classList.add("sticky");
				} else {
					header.classList.remove("sticky");
				}
			}
		};
	}, []);

	return (
		<header id="ht-header" className="ht-header">
			<div className="container">
				<nav data-testid="navbar" className="navbar navbar-default navbar-custom">
					<div className="navbar-header logo">
						<NavLink to="/">
							<img
								className="logo"
								src="images/logo1.png"
								alt=""
								width="119"
								height="58"
							/>
						</NavLink>
					</div>

					<div
						className="collapse navbar-collapse flex-parent"
						id="bs-example-navbar-collapse-1"
					>
						<ul data-testid="nav" className="nav navbar-nav flex-child-menu menu-left">
							<li className="dropdown first">
								<NavLink
									className="btn btn-default"
									to={`/movies`}
									activeClassName="active"
									data-testid="movies-nav-link"
								>
									<i className="fa fa-film"></i> &nbsp; Films
								</NavLink>
							</li>
						</ul>
					</div>
				</nav>
			</div>
		</header>
	);
};

export default Header;
