/**
 * Callculate percent by likes or dislikes count
 *
 * @param {Object} movie
 */
export const calculateBar = (movie) => {
	const likesElem = document.getElementById(`likes-${movie.id}`);
	const dislikesElem = document.getElementById(`dislikes-${movie.id}`);

	if (!likesElem && !dislikesElem) return;
	// Simple math to calculate percentages
	const totalLikes = movie.likes + movie.dislikes;
	const percentLikes = (movie.likes / totalLikes) * 100;
	const percentDislikes = (movie.dislikes / totalLikes) * 100;
	// We need to apply the widths to our elements
	likesElem.style.width = `${!Number.isNaN(percentLikes) ? percentLikes : "50"}%`;
	dislikesElem.style.width = `${!Number.isNaN(percentDislikes) ? percentDislikes : "50"}%`;
};

export const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

/** Default delay for app loading */
export const delayDefaultMS = isTestMode ? 300 : 1000;

export const API_KEY = "aa17687f9e1ac357c0078e6105cae48d"; // I put this here only for test...

export const axiosBaseURL = "https://api.themoviedb.org";

export const axiosRequest = axios.create({
	crossDomain: true,
	baseURL: axiosBaseURL,
	headers: {
		"Content-Type": "application/json;charset=utf-8",
	},
	params: {
		api_key: API_KEY,
		language: "fr-FR",
	},
});

/** In development mode */
const isDevMode = process.env.NODE_ENV === "development";

/** In test mode */
const isTestMode = process.env.NODE_ENV === "test";

/**
 *
 * Get public pathname of app
 */
export const publicPathname = isDevMode ? "/" : "/the-moviedb-reactjs/";

/**
 * Fetch movies data
 *
 * @param {Number} page
 * @param {Function} cb
 */
export const fetchMovies = async (page, cb) => {
	const path = `/3/movie/popular?language=fr-FR&page=${page}&include_image_language=fr,null`;
	const response = await axiosRequest.get(path);
	const data = response.data;

	if (response.status === 200) cb(data, page);
};

/**
 * Reset movies store values
 * @param {*} movies
 */
export const moviesStoreReset = (movies) => {
	movies.map((m) => {
		if (m.color) m.color = null;
	});

	return movies;
};

