import {
	axiosRequest,
	categoriesNames,
	fetchMovies,
	removeCategoriesIfNoMoviesFound,
	delay,
	delayDefaultMS,
	store,
} from "../helpers/helpers";
import {
	SET_MOVIES,
	SET_POPULAR_MOVIES,
	SET_PAGINATION,
	SET_CATEGORIES,
	SET_LOADING,
	SET_MOVIE,
	SET_NBRS_TO_DISPLAY,
	FILTER_BY_CATEGORIES,
	DELETE_MOVIE,
	UPDATE_MOVIES_LIST,
	UPDATE_FILTERED_BY_VALUE,
	UPDATE_LIKED_MOVIES_STORE,
	UPDATE_UNLIKED_MOVIES_STORE,
	UPDATE_TRASH_STORE,
	CLEAR_LIKED_MOVIES_STORE,
	CLEAR_UNLIKED_MOVIES_STORE,
	CLEAR_TRASH_STORE,
	SET_KEEP_MOVIES,
	FILTER_BY_SEARCH,
} from "../constants/action-types";
import { initialState } from "../reducers";

/**
 * Remove the films present in the trash in the movies source
 *
 * @param {Object<Array>} movies
 */
const removeTrashedMovies = async (results) => {
	const storage = JSON.parse(localStorage.getItem(store["trash"])) || [];

	storage.map((movie, i1) =>
		results.map(async (m, i2) => m.id === movie.id && results.splice(i2, 1))
	);
	return results;
};

/**
 *
 * @param {Object<Array>} results
 */
const checkFromStorage = (results) => {
	const likedMovies = JSON.parse(localStorage.getItem(store["likes"])) || [];
	const unlikedMovies = JSON.parse(localStorage.getItem(store["dislikes"])) || [];

	likedMovies.map((movie) => results.map((m, i) => m.id === movie.id && (results[i] = movie)));
	unlikedMovies.map((movie) => results.map((m, i) => m.id === movie.id && (results[i] = movie)));

	return results;
};

/**
 *
 * @param {Object} props
 */
export const paginate = ({ pagination }) => (dispatch) => {
	const { page } = pagination;

	dispatch(setLoading(true));
	fetchMovies(page, (data) => dispatch(setMovies(data, page)));
	return {
		type: SET_PAGINATION,
		payload: { pagination },
	};
};

/**
 * Save movies data and get categories
 *
 * @param {Object<Any>} data
 * @param {Number} page
 */
export const setMovies = (data, page = 1) => async (dispatch) => {
	let movies = [];
	let pagination = initialState.pagination;

	if (data.results.length > 0) {
		movies = checkFromStorage(await removeTrashedMovies(data.results));
		pagination = data;
		pagination.page = page;
		// Define likes, dislikes, likes_dislikes_by_users_ids properties by default
		movies.map((movie, i) => {
			const index = i + 1;

			if (!movie.likes) {
				movie.likes =
					index < 14 ? movie.vote_count : Math.round(movie.vote_count / index) || 0;
			}
			if (!movie.dislikes) {
				movie.dislikes =
					index > 10 ? movie.vote_count : Math.round(movie.vote_count / index) || 0;
			}
		});

		dispatch(getCategories(movies)); // We need categories before all
		await delay(delayDefaultMS);
		dispatch({
			type: SET_MOVIES,
			payload: { movies, pagination },
		});
	}

	dispatch(setLoading(false));
};

