/**
 *
 * @param {Array<Object>} movies
 */
export const setKeepMovies = (movies) => {
	return {
		type: SET_KEEP_MOVIES,
		payload: { movies },
	};
};

/**
 *
 * Get and save categories / genres
 */
export const getCategories = (movies) => async (dispatch) => {
	const path = `/3/genre/movie/list?&language=fr-FR`;
	const response = await axiosRequest.get(path);
	const genres = response.data.genres;
	movies = await movies.map((m) => {
		return Object.assign(m, { categories: categoriesNames(genres, m).filter(Boolean) });
	});

	dispatch(updateMovies(movies));
	return dispatch({
		type: SET_CATEGORIES,
		payload: { categories: genres, movies },
	});
};

/**
 *
 * Filter
 */
export const filterByCategories = (props, filteredBy) => async (dispatch) => {
	const { movies, keepMovies } = props;
	let filteredMovies = [];

	if (process.env.NODE_ENV !== "test") dispatch(setLoading(true)); // Don't need this load in test mode

	if (filteredBy === "reset") {
		dispatch(updateFilteredByValue(""));
		return fetchMovies(1, (data) => dispatch(setMovies(data, 1)));
	} else {
		let _movies = [...movies, ...keepMovies];
		_movies = [...new Set(_movies)];

		dispatch(setKeepMovies(keepMovies.length > 0 ? keepMovies : movies)); // Keep copy
		dispatch(updateFilteredByValue(filteredBy));

		filteredMovies = _movies.filter((m) => {
			return m.categories.includes(filteredBy);
		});
	}

	if (process.env.NODE_ENV !== "test") setTimeout(() => dispatch(setLoading(false)), 1500);

	return dispatch({
		type: FILTER_BY_CATEGORIES,
		payload: {
			movies: filteredMovies,
			filteredBy,
		},
	});
};

/**
 * Update categories
 *
 * @param {Object} categories
 * @param {Object} movies
 */
export const updateCategories = (categories, movies, movie) => {
	categories = removeCategoriesIfNoMoviesFound(categories, movies, movie);

	return {
		type: SET_CATEGORIES,
		payload: { categories },
	};
};
