import React from "react";
import { connect } from "react-redux";
import Swal from "sweetalert";
import { Button, ButtonGroup, UncontrolledTooltip } from "reactstrap";

import {
	setLoading,
	updateMovie,
	updateCategories,
	deleteMovie,
	updateLikedMoviesStore,
	updateUnlikedMoviesStore,
	updateTrashStore,
	updateMovies,
} from "../../actions/index";
import { truncate, calculateBar, store } from "../../helpers/helpers";

import "./Card.css";

const CardComponent = ({ trash = false, ...props }) => {
	const {
		movies,
		updateMovie,
		updateCategories,
		setLoading,
		deleteMovie,
		updateLikedMoviesStore,
		updateUnlikedMoviesStore,
		updateTrashStore,
		updateMovies,
	} = props;
	let [movie, setMovie] = React.useState(props.movie);
	let _movie = props.movie; // We use this copy for mutation
	const [colors, setColors] = React.useState({
		green: "#28a745",
		red: "#dc3545",
		defaultColor: "rgba(0, 0, 0, 0.125)",
	});
	const { poster_path, title } = _movie;
	const movieId = _movie.id;
	const categories = _movie.categories.filter(Boolean).join(", ");
	const handleDelete = () => {
		return Swal({
			title: "Etes-vous sûr de vouloir effectuer cette action ?",
			text: "",
			icon: "warning",
			buttons: {
				cancel: "Annulez",
				confirm: "Oui",
			},
			dangerMode: true,
		}).then(async (ok) => {
			if (ok) {
				const moviesFromRedux = movies;
				const trashStoreName = store["trash"];
				const moviesFromTrash = JSON.parse(localStorage.getItem(trashStoreName)) || [];
				const indexFromRedux = moviesFromRedux.findIndex((m) => m.id === movieId);

				setLoading(true);
				moviesFromRedux.splice(indexFromRedux, 1);
				moviesFromTrash.push(_movie);
				localStorage.setItem(trashStoreName, JSON.stringify(moviesFromTrash)); // Set deleted movie to trash
				updateCategories(props.categories, moviesFromRedux, _movie);
				updateTrashStore(moviesFromTrash);
				removeTrashedInLikesDislikesStore();
				deleteMovie(_movie);
				setLoading(false);
				return Swal("Effacé avec succès !", { icon: "success" });
			}
		});
	};

	/**
	 * Remove trashed movie in likes or dislikes store
	 */
	const removeTrashedInLikesDislikesStore = () => {
		const likesStoreName = store["likes"];
		const dislikesStoreName = store["dislikes"];
		const unlikedFromStorage = JSON.parse(localStorage.getItem(dislikesStoreName)) || [];
		const likedFromStorage = JSON.parse(localStorage.getItem(likesStoreName)) || [];
		const movie1 = likedFromStorage.find((m) => m.id === movieId);
		const movie2 = unlikedFromStorage.find((m) => m.id === movieId);
		// If movie already in likes or dislikes store, REMOVE IT
		if (movie1) {
			const i1 = likedFromStorage.findIndex((m) => m.id === movieId);
			likedFromStorage.splice(i1, 1);
			localStorage.setItem(likesStoreName, JSON.stringify(likedFromStorage)); // Update storage
			updateLikedMoviesStore(likedFromStorage);
		}
		if (movie2) {
			const i2 = unlikedFromStorage.findIndex((m) => m.id === movieId);
			unlikedFromStorage.splice(i2, 1);
			localStorage.setItem(dislikesStoreName, JSON.stringify(unlikedFromStorage));
			updateUnlikedMoviesStore(unlikedFromStorage);
		}
	};

	/**
	 * @param {String} action=like|dislike
	 */
	const handleLikeDislike = (action) => {
		const alreadyLiked = movie.color === colors.green;
		const alreadyDisLiked = movie.color === colors.red;
		// If user want to redo the same action
		if (alreadyLiked && action === "like") return;
		if (alreadyDisLiked && action === "dislike") return;

		const isLike = action === "like";
		let title = isLike ? `Vous aimez '${movie.title}'` : `Vous aimez pas '${movie.title}'`;
		const altKeyStorage = alreadyLiked ? "like" : "dislike";
		const altKeyButton = isLike ? "dislike" : "like";
		const button = document.getElementById(`card-${action}-btn-${movieId}`);
		const altButton = document.getElementById(`card-${altKeyButton}-btn-${movieId}`);
		const icon = isLike ? `success` : `error`;
		const storage = JSON.parse(localStorage.getItem(store[`${action}s`])) || [];
		const altStorage = JSON.parse(localStorage.getItem(store[`${altKeyStorage}s`])) || [];
		const color = isLike ? colors.green : colors.red;
		const {
			location: { pathname },
		} = props;
		// Update buttons
		button.disabled = true;
		altButton.disabled = false;

		button.classList.add("disabled");
		altButton.classList.remove("disabled");
		// Set new values
		_movie = Object.assign(movie, {
			color,
			[`${action}s`]: movie[`${action}s`] + 1,
		});
		// If movie's already liked remove it in the likeStore and vice versa
		const altIndex = altStorage.findIndex((m) => m.id === movieId);

		if (altIndex !== -1) {
			altStorage.splice(altIndex, 1);
			localStorage.setItem(store[`${altKeyStorage}s`], JSON.stringify(altStorage));
		}
		// Update state and store
		setMovie({ ..._movie });
		storage.push(_movie);
		localStorage.setItem(store[`${action}s`], JSON.stringify(storage)); // Update storage
		updateMovie(_movie); // Update state
		calculateBar(_movie);
		return Swal({ title, text: "", icon });
	};

	/**
	 * Cancel like or dislike action
	 */
	const handleCancel = () => {
		const isLiked = movie.color === colors.green;
		const action = isLiked ? "like" : "dislike";
		const storeKey = isLiked ? "likes" : "dislikes";
		const storage = JSON.parse(localStorage.getItem(store[storeKey])) || [];
		const index = storage.findIndex((m) => m.id === movieId);
		const button = document.getElementById(`card-${action}-btn-${movieId}`);
		_movie = Object.assign(_movie, {
			color: colors.defaultColor,
			[storeKey]: movie[storeKey] - 1,
		});
		// Update view
		setMovie({ ..._movie });
		button.classList.remove("disabled");
		button.removeAttribute("disabled");
		updateMovie(_movie); // Update state
		calculateBar(_movie);
		// Remove in related store
		storage.splice(index, 1);
		localStorage.setItem(store[storeKey], JSON.stringify(storage)); // Update storage
		if (action === "like") updateLikedMoviesStore(storage);
		if (action === "dislike") updateUnlikedMoviesStore(storage);
		return Swal("Votre action a été annulée", { icon: "success" });
	};

	/**
	 * Restore movie from trash
	 */
	const handleRestore = () => {
		const trashStoreName = store["trash"];
		const moviesFromTrash = JSON.parse(localStorage.getItem(trashStoreName)) || [];
		const index = moviesFromTrash.findIndex((m) => m.id === movieId);

		moviesFromTrash.splice(index, 1);
		updateTrashStore(moviesFromTrash);
		updateMovies([...moviesFromTrash, ...movies]);
		localStorage.setItem(trashStoreName, JSON.stringify(moviesFromTrash)); // Set deleted movie to trash
		return Swal({
			title: `${movie.title} a été restauré avec succès !`,
			text: "",
			icon: "success",
		});
	};

	/**
	 * Display movie info
	 */
	const goToDetail = () => props.history.push(`/movies/${movieId}`);

	React.useEffect(() => {
		calculateBar(movie);
	}, [movie]);

	const style = {
		border: "2px solid " + movie.color,
		boxShadow: "1px 1px 4px 0px " + movie.color,
	};

	return (
		<div data-testid="card" id={`card-${movieId}`} className="movie-card-container">
			<div className="movie-card-content">
				<div className="movie-card-content-overlay"></div>
				<img
					style={style}
					className="movie-card-image"
					src={"https://image.tmdb.org/t/p/original/" + poster_path}
				/>
				<div className="movie-card-content-details movie-card-fadeIn-top">
					<h3>{title}</h3>
					<p>
						{_movie.overview
							? truncate(_movie.overview, 30, "...")
							: "Aucune description"}
					</p>
					<div className="card-custom-footer">
						<ButtonGroup size="sm">
							{!trash ? (
								<>
									<Button
										onClick={goToDetail}
										className="card-details-btn"
										id={`card-details-btn-${movieId}`}
										title="Détails"
									>
										<i className="fa fa-eye"></i>
									</Button>
									{_movie.color !== colors.green && (
										<Button
											onClick={() => {
												handleLikeDislike("like");
											}}
											className="card-like-btn"
											id={`card-like-btn-${movieId}`}
											title="J'aime"
										>
											<i className="fa fa-thumbs-up"></i>
										</Button>
									)}
									{_movie.color !== colors.red && (
										<Button
											onClick={() => {
												handleLikeDislike("dislike");
											}}
											className="card-dislike-btn"
											id={`card-dislike-btn-${movieId}`}
											title="J'aime pas"
										>
											<i className="fa fa-thumbs-down"></i>
										</Button>
									)}
									{_movie.color && _movie.color !== colors.defaultColor && (
										<Button onClick={() => handleCancel()} title="Annulez">
											<i className="fa fa-undo"></i>
										</Button>
									)}
									<Button onClick={handleDelete} className="card-delete-btn">
										<i className="fa fa-trash"></i>
									</Button>
								</>
							) : (
								<Button
									onClick={handleRestore}
									className="card-remove-from-trash-btn"
									id={`card-remove-from-trash-btn-${movieId}`}
									title="Retirer de la corbeille"
									color="warning"
								>
									<small>Restaurer</small>
								</Button>
							)}
						</ButtonGroup>
					</div>
				</div>
			</div>

			<div className="movie-card-stat">
				<div className="thumbs-icons">
					<i className="fa fa-thumbs-up"></i> &nbsp;&nbsp; <span>{_movie.likes}</span>
					&nbsp; / &nbsp;
					<i className="fa fa-thumbs-down"></i> &nbsp;&nbsp;{" "}
					<span>{_movie.dislikes}</span>
				</div>
				<div className="percent-bar">
					<div className="likes" id={`likes-${movieId}`}></div>
					<div className="dislikes" id={`dislikes-${movieId}`}></div>
				</div>
			</div>

			<div className="movie-card-categories">
				<UncontrolledTooltip placement="top" target={`ctg-tooltip-${movieId}`}>
					{categories}
				</UncontrolledTooltip>
				<h6 id={`ctg-tooltip-${movieId}`} className="card-movie-subtitle">
					{categories}
				</h6>
			</div>
		</div>
	);
};

const mapStateToProps = (state) => ({
	movies: state.movies,
	categories: state.categories,
});

const mapDispatchToProps = {
	updateMovie,
	updateCategories,
	deleteMovie,
	updateLikedMoviesStore,
	updateUnlikedMoviesStore,
	updateTrashStore,
	updateMovies,
	setLoading,
};

export default connect(mapStateToProps, mapDispatchToProps)(CardComponent);
