import React from "react";
import { TabContent, TabPane, Nav, NavItem, NavLink, Container } from "reactstrap";

import LikedUnlikedMoviesComponent from "./components/LikedUnlikedMovies.jsx";
import Trash from "./components/Trash.jsx";

import "./Historical.css";

const HistoricalComponent = (props) => {
	const [activeTab, setActiveTab] = React.useState("1");

	React.useEffect(() => {}, [activeTab]);

	return (
		<Container className="historical">
			<Nav tabs>
				<NavItem>
					<NavLink
						className={`${activeTab === "1" ? "active" : ""}`}
						onClick={() => setActiveTab("1")}
					>
						<i className="fa fa-thumbs-up"></i> &nbsp; Films aimés
					</NavLink>
				</NavItem>
				<NavItem>
					<NavLink
						className={`${activeTab === "2" ? "active" : ""}`}
						onClick={() => setActiveTab("2")}
					>
						<i className="fa fa-thumbs-down"></i> &nbsp; Films non aimés
					</NavLink>
				</NavItem>
				<NavItem>
					<NavLink
						className={`${activeTab === "3" ? "active" : ""}`}
						onClick={() => setActiveTab("3")}
					>
						<i className="fa fa-trash"></i> &nbsp; Corbeille
					</NavLink>
				</NavItem>
			</Nav>
			<TabContent activeTab={activeTab}>
				<TabPane tabId="1">
					{activeTab === "1" && (
						<LikedUnlikedMoviesComponent {...props} componentName="likedMovies" />
					)}
				</TabPane>
				<TabPane tabId="2">
					{activeTab === "2" && (
						<LikedUnlikedMoviesComponent {...props} componentName="unlikedMovies" />
					)}
				</TabPane>
				<TabPane tabId="3">{activeTab === "3" && <Trash {...props} />}</TabPane>
			</TabContent>
		</Container>
	);
};

export default HistoricalComponent;
