import React from "react";
import { connect } from "react-redux";
import Slider from "react-slick";
import { NavLink } from "react-router-dom";

import "./Slider.css";

const SliderComponent = (props) => {
	const { movies } = props;
	const reverseMovies = movies.slice().reverse();
	const settings = {
		infinite: true,
		slidesToShow: 3,
		slidesToScroll: 1,
		centerMode: true,
		draggable: true,
		autoplay: true,
		autoplaySpeed: 4000,
		arrows: false,
		dots: false,
		responsive: [
			{
				breakpoint: 768,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 2,
				},
			},
			{
				breakpoint: 480,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
				},
			},
		],
	};
	const categories = (categories) => {
		return categories.map((c, i) => {
			return (
				<div key={c} className="cate">
					<span className="blue">{c}</span>
				</div>
			);
		});
	};

	return (
		<div className="slider movie-items">
			<div className="container">
				<div className="row">
					<div className="slick-multiItemSlider">
						<Slider {...settings}>
							{reverseMovies.map((movie, i) => {
								if (i <= 6)
									return (
										<div key={movie.id} className="movie-item">
											<div className="mv-img">
												<NavLink to={`/movies/${movie.id}`}>
													<img
														src={
															"https:image.tmdb.org/t/p/original/" +
															movie.poster_path
														}
														alt={movie.title}
														width="285"
														height="437"
													/>
												</NavLink>
											</div>
											<div className="title-in">
												{categories(movie.categories)}
												<h6>
													<a href="#">{movie.title}</a>
												</h6>
											</div>
										</div>
									);
							})}
						</Slider>
					</div>
				</div>
			</div>
		</div>
	);
};

const mapStateToProps = (state) => ({
	movies: state.movies,
	loadingApp: state.loadingApp,
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(SliderComponent);
