import React from "react";
import { Row, Col } from "reactstrap";
import Swal from "sweetalert";
import { connect } from "react-redux";
import ClipLoader from "react-spinners/ClipLoader";

import Card from "../../Card/Card.jsx";

import {
	clearLikedMoviesStore,
	updateLikedMoviesStore,
	updateUnlikedMoviesStore,
	clearUnlikedMoviesStore,
	updateMovies,
} from "../../../actions/index.js";

import { store, override } from "../../../helpers/helpers.js";

const LikedUnlikedMoviesComponent = ({ componentName, ...props }) => {
	const isLiked = componentName === "likedMovies";
	const {
		clearLikedMoviesStore,
		updateLikedMoviesStore,
		updateUnlikedMoviesStore,
		clearUnlikedMoviesStore,
		likedMoviesStore,
		unlikedMoviesStore,
	} = props;
	const [loading, setLoading] = React.useState(true);
	const id = isLiked ? "likes" : "dislikes";
	const handleClearStore = () => {
		return Swal({
			title: "Etes-vous sûr de vouloir effectuer cette action ?",
			text: "",
			icon: "warning",
			buttons: {
				cancel: "Annulez",
				confirm: "Oui",
			},
			dangerMode: true,
		}).then((ok) => {
			isLiked ? clearLikedMoviesStore() : clearUnlikedMoviesStore();
		});
	};
	const movies = isLiked ? likedMoviesStore : unlikedMoviesStore;

	React.useEffect(() => {
		const fromStorage = JSON.parse(window.localStorage.getItem(store[id])) || [];

		isLiked ? updateLikedMoviesStore(fromStorage) : updateUnlikedMoviesStore(fromStorage);
		setTimeout(() => setLoading(false), 1000);
	}, []);

	if (loading) return <ClipLoader css={override} size={150} loading={loading} />;

	return (
		<div id={id}>
			{movies.length > 0 ? (
				<>
					<button
						onClick={handleClearStore}
						type="button"
						className="btn btn-warning clear-store"
					>
						Videz le store
					</button>

					<hr />

					<Row>
						{movies.map((movie, i) => (
							<Col md="4" key={i}>
								<Card i={i} {...props} movie={movie} />
							</Col>
						))}
					</Row>
				</>
			) : (
				<div className="nothing-to-display">Aucun contenu à afficher...</div>
			)}
		</div>
	);
};

const mapStateToProps = (state) => ({
	likedMoviesStore: state.likedMoviesStore,
	unlikedMoviesStore: state.unlikedMoviesStore,
});
const mapDispatchToProps = {
	updateLikedMoviesStore,
	clearLikedMoviesStore,
	updateUnlikedMoviesStore,
	clearUnlikedMoviesStore,
	updateMovies,
};

export default connect(mapStateToProps, mapDispatchToProps)(LikedUnlikedMoviesComponent);
