import React from "react";
import { Container } from "reactstrap";
import { connect } from "react-redux";
import ClipLoader from "react-spinners/ClipLoader";

import { axiosRequest, override } from "../../helpers/helpers.js";

import "./Movies.css";

const DetailMovieComponent = (props) => {
	const [loading, setLoading] = React.useState(true);
	const [movie, setMovie] = React.useState({});
	/**
	 * Fetch movie if movies's empty or user refresh the page without pass by movies component
	 */
	const fetchMovie = async (movieId) => {
		const path = `/3/movie/${movieId}?language=fr-FR&include_image_language=fr,null`;
		const response = await axiosRequest.get(path);
		const movie = response.data;

		if (response.status === 200) setMovie(movie);
		setTimeout(() => setLoading(false), 1000);
	};

	React.useEffect(() => {
		const { match } = props;
		const movieId = match.params.id;

		fetchMovie(movieId);
	}, []);

	if (loading) return <ClipLoader css={override} size={150} loading={loading} />;

	const categories = movie["genres"].map((g) => g.name);

	return (
		<Container className="movie-detail-container">
			{movie ? (
				<div className="row">
					<div className="col-lg-4 col-md-5">
						<div>
							<img
								alt={movie.original_title}
								src={"https://image.tmdb.org/t/p/original/" + movie.poster_path}
							/>
						</div>
						<div className="release">
							<h4>{movie.release_date}</h4>
							<p className="sm-width-95 sm-margin-auto">{movie.tagline}</p>
						</div>
					</div>

					<div className="col-lg-8 col-md-7">
						<div>
							<h3>
								<strong>{movie.original_title}</strong>
							</h3>
							<p className="overview">{movie.overview}</p>
							<div className="contact-info-section">
								<ul>
									{categories.length > 0 && (
										<li>
											<div className="row">
												<div className="col-md-5 col-5">
													<strong>Catégories:</strong>
												</div>
												<div className="col-md-7 col-7">
													<p>
														{categories.length > 0
															? categories.join(", ")
															: ""}
													</p>
												</div>
											</div>
										</li>
									)}
								</ul>
							</div>
						</div>
					</div>
				</div>
			) : (
				<div className="nothing-to-display">
					Aucun contenu à afficher. L'élément n'existe peut-être plus.
				</div>
			)}
		</Container>
	);
};

const mapStateToProps = (state) => ({
	movies: state.movies,
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(DetailMovieComponent);
