import {
	SET_CATEGORIES,
	SET_LOADING,
	SET_MOVIE,
	SET_MOVIES,
	SET_KEEP_MOVIES,
	SET_PAGINATION,
	SET_NBRS_TO_DISPLAY,
	FILTER_BY_CATEGORIES,
	DELETE_MOVIE,
	UPDATE_MOVIES_LIST,
	UPDATE_FILTERED_BY_VALUE,
	UPDATE_LIKED_MOVIES_STORE,
	UPDATE_UNLIKED_MOVIES_STORE,
	UPDATE_TRASH_STORE,
	CLEAR_LIKED_MOVIES_STORE,
	CLEAR_UNLIKED_MOVIES_STORE,
	CLEAR_TRASH_STORE,
	FILTER_BY_SEARCH,
} from "../constants/action-types";

import { moviesStoreReset } from "../helpers/helpers"

const initialState = {
	movies: [],
	keepMovies: [],
	filteredMovies: [],
	categories: [],
	loadingApp: true,
	unlikedMoviesStore: [],
	likedMoviesStore: [],
	trashStore: [],
	itemToDisplay: 12,
	filteredBy: "",
	searchValue: "",
	pagination: {
		page: 1,
		total_pages: 500,
	},
};

// Each action will be called manually
const rootReducer = (state = initialState, action) => {
	if (action.type === SET_MOVIES) {
		const movies = action.payload.movies;

		return {
			...state,
			movies,
			pagination: {
				...state.pagination,
				...action.payload.pagination,
			},
		};
	} else if (action.type === FILTER_BY_SEARCH) {
		const value = action.payload.value;
		let filteredMovies = state.movies.filter((m) =>
			m.title.toLowerCase().includes(value.toLowerCase())
		);

		if (value === "") {
			filteredMovies = [];
		}
		return { ...state, searchValue: value, filteredMovies };
	} else if (action.type === SET_KEEP_MOVIES) {
		const movies = action.payload.movies;

		return { ...state, keepMovies: movies };
	} else if (action.type === FILTER_BY_CATEGORIES) {
		const movies = action.payload.movies;
		const filteredBy = action.payload.filteredBy;

		return { ...state, movies, filteredBy };
	} else if (action.type === UPDATE_MOVIES_LIST) {
		const movies = state.movies;

		return { ...state, movies };
	} else if (action.type === SET_MOVIE) {
		const movie = action.payload.movie;
		const index = state.movies.findIndex((m) => m.id === movie.id);
		const movies = state.movies;
		movies[index] = movie;

		return { ...state, movies };
	} else if (action.type === DELETE_MOVIE) {
		const movie = action.payload.movie;
		const movies = state.movies;
		const filtered = movies.filter((m) => m.id !== movie.id);

		return { ...state, movies: filtered };
	} else if (action.type === SET_CATEGORIES) {
		const categories = action.payload.categories;

		return { ...state, categories };
	} else if (action.type === SET_LOADING) {
		return Object.assign({}, state, {
			loadingApp: action.payload.loadingApp,
		});
	} else if (action.type === SET_PAGINATION) {
		return {
			...state,
			pagination: {
				...state.pagination,
				...action.payload.pagination,
			},
		};
	} else if (action.type === UPDATE_FILTERED_BY_VALUE) {
		const filteredBy = action.payload.filteredBy;

		return { ...state, filteredBy };
	} else if (action.type === SET_NBRS_TO_DISPLAY) {
		return { ...state, itemToDisplay: action.payload.itemToDisplay };
	} else if (action.type === UPDATE_LIKED_MOVIES_STORE) {
		return { ...state, likedMoviesStore: action.movies };
	} else if (action.type === UPDATE_UNLIKED_MOVIES_STORE) {
		return { ...state, unlikedMoviesStore: action.movies };
	} else if (action.type === UPDATE_TRASH_STORE) {
		return { ...state, trashStore: action.movies };
	} else if (action.type === CLEAR_LIKED_MOVIES_STORE) {
		return { ...state, movies: moviesStoreReset(state.movies), likedMoviesStore: [] };
	} else if (action.type === CLEAR_UNLIKED_MOVIES_STORE) {
		return { ...state, movies: moviesStoreReset(state.movies), unlikedMoviesStore: [] };
	} else if (action.type === CLEAR_TRASH_STORE) {
		return { ...state, trashStore: [] };
	}

	return state;
};

export { initialState };
export default rootReducer;
